import React from "react";
import { Form, Input, Radio, Select, Rate, Checkbox, Button } from "antd";
import { useState } from "react";
export default function ProfileForm() {
  const [FormData, setFormData] = useState([]);
  const { Option } = Select;
  // const newFormData = [];
  const onFinish = (values) => {
    setFormData(
      FormData.concat({
        ...values,
        skills: {
          professional: values.skills.professional.toString(),
          computer: values.skills.computer.toString(),
          leadership: values.skills.leadership.toString(),
          timeManagement: values.skills.timeManagement.toString(),
          communication: values.skills.communication.toString(),
          ...values.skills,
        },
      })
    );
    console.log("", values);
  };

  return (
    <div>
      <Form
        layout="vertical"
        onFinish={onFinish}
        initialValues={{
          // 
          gender: "male",
          // 
          education: "bachelor",
          // 
          skills: {
            // 
            professional: 3,
            // 
            computer: 3,
            // 
            leadership: 3,
            // 
            timeManagement: 3,
            // 
            communication: 3,
          },
        }}
// 600
        style={{ maxWidth: 350, margin: "0 auto" }}
      >
        <Form.Item
          label=""
          name="name"
          rules={[{ required: true, message: "" }]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="gender"
          rules={[{ required: true, message: "" }]}
        >
          <Radio.Group>
            <Radio value={"male"}></Radio>
            <Radio value={"female"}></Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item
          label=""
          name="age"
          rules={[{ required: true, message: "" }]}
        >
          <Input placeholder="" type="number" />
        </Form.Item>
        <Form.Item
          label=""
          name="phone"
          rules={[
            { required: true, message: "" },
            {
              // 
              validator: (_, value) => {
                // 
                const phoneRegex = /^1[3-9]\d{9}$/;
                // 
                if (!value || phoneRegex.test(value)) {
                  return Promise.resolve();
                }
                // 
                return Promise.reject(
                  new Error("")
                );
              },
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          label=""
          name="email"
          rules={[
            { required: true, type: "email", message: "" },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          label="/"
          name="department"
          rules={[{ required: true, message: "/" }]}
        >
          <Input placeholder="/" />
        </Form.Item>

        <Form.Item
          label=""
          name="yearsOfWork"
          rules={[{ required: true, message: "" }]}
        >
          <Input placeholder="" type="number" />
        </Form.Item>
        <h2>2. </h2>

        <Form.Item label="" name="education">
          <Select>
            <Option value="highschool"></Option>
            <Option value="associate"></Option>
            <Option value="bachelor"></Option>
            <Option value="master"></Option>
            <Option value="doctor"></Option>
          </Select>
        </Form.Item>

        <Form.Item label="" name="major">
          <Input placeholder="" />
        </Form.Item>

        <Form.Item label="" name="school">
          <Input placeholder="" />
        </Form.Item>

        <h2>3. </h2>

        <Form.Item label="" name="jobSatisfaction">
          <Rate />
        </Form.Item>

        <Form.Item label="" name="workload">
          <Radio.Group>
            <Radio value="tooMuch"></Radio>
            <Radio value="justRight"></Radio>
            <Radio value="tooLittle"></Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item label="" name="teamWork">
          <Rate />
        </Form.Item>

        <Form.Item label="" name="managementSupport">
          <Rate />
        </Form.Item>

        <h2>4. </h2>

        <Form.Item label="" name={["skills", "professional"]}>
          <Rate />
        </Form.Item>

        <Form.Item label="" name={["skills", "computer"]}>
          <Rate />
        </Form.Item>

        <Form.Item label="" name={["skills", "leadership"]}>
          <Rate />
        </Form.Item>

        <Form.Item label="" name={["skills", "timeManagement"]}>
          <Rate />
        </Form.Item>

        <Form.Item label="" name={["skills", "communication"]}>
          <Rate />
        </Form.Item>

        {/* 5.  */}
        <h2>5. </h2>

        <Form.Item label="" name="willingToTrain">
          <Radio.Group>
            <Radio value="yes"></Radio>
            <Radio value="no"></Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item label="" name="trainingTypes">
          <Checkbox.Group>
            <Checkbox value="professionalSkills"></Checkbox>
            <Checkbox value="leadership"></Checkbox>
            <Checkbox value="projectManagement"></Checkbox>
            <Checkbox value="communication"></Checkbox>
          </Checkbox.Group>
        </Form.Item>

        <Form.Item label="" name="careerGoal">
          <Input.TextArea placeholder="" />
        </Form.Item>

        {/* 6.  */}
        <h2>6. </h2>

        <Form.Item label="" name="workEnvironment">
          <Rate />
        </Form.Item>

        <Form.Item label="" name="companyBenefits">
          <Rate />
        </Form.Item>

        <Form.Item
          label=""
          name="workplaceSafety"
        >
          <Rate />
        </Form.Item>

        {/* 7.  */}
        <h2>7. </h2>

        <Form.Item label="" name="improvements">
          <Input.TextArea placeholder="" />
        </Form.Item>

        <Form.Item label="" name="developmentSuggestions">
          <Input.TextArea placeholder="" />
        </Form.Item>

        {/* 8.  */}
        <h2>8. </h2>

        <Form.Item label="" name="furtherFeedback">
          <Radio.Group>
            <Radio value="yes"></Radio>
            <Radio value="no"></Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}
